/*
================================================================================
|                                                                              |
|                             RETRO FLUID DESIGN BLUEPRINT                     |
|                                                                              |
--------------------------------------------------------------------------------
|
| Core Concept:
| A fusion of 90s blocky, text-heavy web aesthetics with modern design's
| fluidity, minimalism, and dynamic effects. The goal is to evoke a sense of
| nostalgia while feeling fresh, responsive, and interactive.
|
|-------------------------------------------------------------------------------
|
| Visual Themes:
|
| 1. Glassy and Fluid Backgrounds:
|    - Backgrounds feature slow-moving, subtle gradients that animate
|      continuously, creating a "lava lamp" or "fluid" effect. This provides
|      a dynamic, non-static canvas for the content.
|
| 2. Blocky and Structured Foregrounds:
|    - Content containers have defined borders and soft box-shadows, giving
|      them a tangible, "card-like" feel that floats above the fluid background.
|    - Monospace fonts are used for titles and headers to reinforce the retro,
|      terminal-like aesthetic.
|
| 3. Decorative and Interactive Text:
|    - Text is not just for reading; it's part of the UI. Links and
|      interactive elements have prominent text decorations that animate on
|      interaction. Hover and click states are obvious and satisfying.
|
|-------------------------------------------------------------------------------
|
| Color Palette (Light Mode):
| - Background: Soft, desaturated pastel gradients (lavender, peach, mint).
| - Foreground: Off-white or very light gray cards with a glassy effect.
| - Text: Dark charcoal or deep navy.
| - Accent: A vibrant, slightly neon color (electric blue) for links/highlights.
|
|-------------------------------------------------------------------------------
|
| Typography:
| - Titles/Headers: Monospace, bold, with generous letter-spacing.
| - Body/Links: A clean, readable sans-serif font.
|
|-------------------------------------------------------------------------------
|
| Animation & Interactivity:
| - Passive: The main background gradient is constantly in motion.
| - Entrance: Elements fade in and slide up slightly upon page load.
| - Hover: Links get an animated underline; cards lift slightly.
| - Active/Click: Buttons have a "press down" effect.
|
================================================================================
*/

/* --- Keyframe Animations --- */
@keyframes fluid-background {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* --- Base Styles --- */
.page {
  font-family: var(--font-sans);
  color: var(--color-text);
  background: linear-gradient(
    -45deg,
    var(--color-background-start),
    var(--color-background-mid),
    var(--color-background-end)
  );
  background-size: 400% 400%;
  animation: fluid-background 20s ease infinite;
  min-height: 100vh;
  padding-top: var(
    --header-height
  ); /* Prevent content from hiding under fixed header */
}

/* --- Header & Controls --- */
.header {
  font-family: var(--font-mono);
  font-weight: bold;
  font-size: 1.25rem;
  letter-spacing: 1px;
  text-align: center;
  padding: calc(var(--spacing-unit) * 2);
  color: var(--color-text);
  text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.5);
}

.controls {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: calc(var(--spacing-unit) * 2);
  padding: calc(var(--spacing-unit) * 2);
  height: var(--header-height);

  /* Glassmorphism Effect */
  background: var(--color-card-background);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--color-card-border);
  box-shadow: 0 4px 30px var(--color-shadow);
}

.button {
  font-family: var(--font-mono);
  font-size: 1rem;
  text-decoration: none;
  color: var(--color-text);
  background-color: transparent;
  padding: var(--spacing-unit) calc(var(--spacing-unit) * 3);
  border: 1px solid var(--color-text);
  border-radius: 4px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition:
    color var(--transition-speed-fast) ease,
    transform var(--transition-speed-fast) ease;
}

.button::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background-color: var(--color-accent);
  transition: left var(--transition-speed-slow) ease;
  z-index: -1;
}

.button:hover {
  color: white;
}

.button:hover::after {
  left: 0;
}

.button:active {
  transform: scale(0.97);
}

/* --- Main Content Area --- */
.content {
  padding: calc(var(--spacing-unit) * 4);
  max-width: 900px;
  margin: 0 auto;
}

.previews {
  display: grid;
  gap: calc(var(--spacing-unit) * 4);
}

/* This assumes ArticlePreviewComponent has a root element that will receive these styles */
.previews > * {
  /* Entrance Animation */
  animation: fade-in-up 0.6s ease-out forwards;
  opacity: 0; /* Start hidden */
}

/* Stagger the animation for a nicer effect */
.previews > *:nth-child(1) {
  animation-delay: 0.1s;
}
.previews > *:nth-child(2) {
  animation-delay: 0.2s;
}
.previews > *:nth-child(3) {
  animation-delay: 0.3s;
}
/* Add more if you expect more than 3 previews per page */
