/* --- CSS Variables --- */
:root {
  /* Fonts */
  --font-sans:
    "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --font-mono:
    "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;

  /* Light Mode Colors */
  --color-text: #1a202c;
  --color-background-start: #d2d6df;
  --color-background-mid: #eaf2f8;
  --color-background-end: #d6eaf8;
  --color-accent: #3498db;
  --color-accent-glow: rgba(52, 152, 219, 0.5);
  --color-card-background: rgba(255, 255, 255, 0.7);
  --color-card-border: #ffffff;
  --color-shadow: rgba(45, 55, 72, 0.2);

  /* Spacing & Sizing */
  --spacing-unit: 8px;
  --header-height: 120px; /* Height for fixed header/controls area */

  /* Animation */
  --transition-speed-fast: 0.2s;
  --transition-speed-slow: 0.4s;
}
